body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
/* ================================Navbar Start====================================== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Mulish", sans-serif;
}

.navbar {
  background-color: #fff !important;
}

.navbar-brand {
  color: #565387 !important;
  font-size: 1.9rem !important;
}

.navbar a {
  text-transform: capitalize;
}

.menu_active {
  font-weight: bold;
  border-bottom: 1px solid #565387;
}

.navbar a:hover {
  color: #3498db !important;
}
/* ================================Navbar End====================================== */

/* ================================Homepage Start====================================== */
.brand_name {
  color: #3498db;
}
#header {
  margin-top: 90px;
  width: 100%;
  height: 80vh;
}
#header h2 {
  font-size: 25px;
}
.btn-primary {
  background: #fff;
  color: #3498db;
  transition: 0.5s;
  border: 2px solid #3498db;
}
.btn-primary:hover {
  background: #3498db;
  color: #fff;
}
.header-img {
  text-align: right;
}
#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}
.contact_div {
  margin-bottom: 33px;
}

@-webkit-keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    transform: translateY(10px);
  }
  100% {
    transform: translateY(-10px);
  }
}
/* ================================Homepage End====================================== */

/* ================================Responsive CSS====================================== */
@media (max-width: 991px) {
  .navbar-brand {
    font-size: 1.5rem;
  }

  .menu_active {
    font-weight: bold;
    border-bottom: none;
  }
  #header {
    height: 80vh;
    text-align: center;
  }
  #header .header-img {
    text-align: center;
}
#header .header-img img {
  width: 60%;
}
}

@media (max-width: 768px) {
  #header {
    margin-top: 30px;
  }
  .col-md-6 {
    width: 100% !important;
  }
  #header h2 {
    font-size: 18px;
    margin-bottom: 30px;
  }
  #header .header-img img {
    width: 70%;
  }
  .navbar-brand{
    margin-left: 0px;
  }
  .text-center{
    margin: 0 20px !important;
  }
}
